	module.exports = {"data":{"wordpressPost":{"title":"Open Access Week: the next generation","content":"<p><a href=\"http://www.openaccessweek.org/\">Open Access Week</a>, a global event now entering its eighth year and running from the 20th – 26th October, is an opportunity for the academic and research community to continue to learn about the potential benefits of Open Access, to share what they’ve learned with colleagues, and to help inspire wider participation in helping to make Open Access a new norm in scholarship and research. <em>BMJ Open</em> takes a closer look at this year’s theme as announced by <a href=\"http://www.sparc.arl.org/initiatives/openaccessweek/2014/announcement\">The Scholarly Publishing and Academic Resources Coalition (SPARC)</a> – “Generation Open”. As explained by SPARC, this theme represents the role of the next generation of Open Access advocates, and also what impact any changes within scholarly publishing have upon the careers of scholars and researchers.</p>\n<p>So why do we still need an annual Open Access Week? For a long time, establishing the rules and even the definition of Open Access remained an absolute necessity. The radical departure from traditional publishing models meant that early Open Access advocates had to face legitimate questions over the funding and sustainability of this business model, and address the possibility of its misuse (for example, in so-called ‘vanity publishing’). Such questions do, to a point, remain. Educating the publishing community about Open Access, however, has led to so much support that ideas including Open Access Week (and the <a href=\"http://blog.openaccessbutton.org/about/\">Open Access Button</a>) were able to flourish and garner a significant following of their own, enabling future generations to better inform others and build upon these foundations. In fact, the team behind the simple genius of the Open Access Button, where being unable to access a research article because of a paywall can be reported on, are/were largely <a href=\"http://blog.openaccessbutton.org/team/\">students </a>– how’s about that for ‘Generation Open’?!</p>\n<p>At <em>BMJ Open</em> we welcome submissions from students as well as more established authors. We fully support Open Access Week and as such we’ve created a special landing <a href=\"http://promotions.bmj.com/oa/open-access-week-at-bmj/\">page </a>with some of our most read Open Access content and are offering a 15% discount on article publishing charges on all our fully open and hybrid titles between the 20th October and the 20th November.</p>\n<p>Similarly, the shift by many publishers to Open Access continues unabated with hundreds of Open Access journals launched each year by several global publishers offering <a href=\"http://en.wikipedia.org/wiki/Open_access\">gold, green and hybrid Open Access</a> options. Importantly, can and will government and institutional policy reflect this to also benefit future researchers who wish to make their work fully accessible? Well, policies introduced within the last few years are on their side. Relatively recent Open Access mandates from organisations such as <a href=\"http://www.rcuk.ac.uk/research/openaccess/\">Research Councils UK</a> (with grant funding for gold Open Access) and the <a href=\"http://www.whitehouse.gov/blog/2013/02/22/expanding-public-access-results-federally-funded-research\">US government</a> (with mandated deposition in public repositories, i.e. green Open Access) will no doubt be continually revised or perhaps even superseded to accommodate the needs of funders, researchers and publishers.</p>\n<p>So, as we enter Open Access Week, what will the future hold for Open Access? Will the benefits still need espousing? Will we even need ‘advocates’ or will the foundations of the movement be so ingrained upon future researchers that the days of defending Open Access be reduced to just memories of a different era? Given initial, and, arguably, continuing scepticism, the fact that we are looking to the next generation of scholars to steer the Open Access movement forward is an achievement in itself. The future milestones they will reach and the innovations they will deliver have not yet been determined. Maybe the foundations will be laid at this year’s Open Access Week, but we wait with anticipation for where the next generation of Open Access leaders will take us.</p>\n<p>And we will be ready to support them.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby for BMJ blog sites"}}},"pathContext":{"id":"51479f0f-5e67-5b18-beda-1208ed4aa8ce"}}

